import Vue from 'vue'
import Router from 'vue-router'
import LayoutMain from '../components/layout/Main'
import Home from '../views/Home'
import Feature from '../views/Feature'
import ProjectList from '../views/ProjectList'
import Schedule from '../views/Schedule'
import AddLabor from '../views/AddLabor'
import SelectWorkType from '../views/SelectWorkType'

const TaskTrace = () => (
    /* webpackChunkName: "TaskTrace" */
    import('../views/TaskTrace')
)

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    component: LayoutMain,
    children: [{
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/feature',
      name: 'feature',
      component: Feature
    }
    ]
  },
  {
    path: '/taskTrace',
    name: 'taskTrace',
    component: TaskTrace
  },
  {
    path: '/projects',
    name: 'projects',
    component: ProjectList
  },
  {
    path: '/schedule',
    name: 'schedule',
    component: Schedule
  },
  {
    path: '/addlabor',
    name: 'addlabor',
    component: AddLabor
  },
  {
    path: '/selectWorkType',
    name: 'selectWorkType',
    component: SelectWorkType
  }
  ]
})



