export default {
  name: 'Worktype',
  props: {
    ob: {
      type: Object,
      // 对象或数组默认值必须从一个工厂函数获取
      default: function () {
        return {
          id: '5917242334161117925',
          name: '张波',
          visible: true
        }
      }
    }
  },
  data () {
    return {
    }
  },
  methods: {
    checked (e) {
          // console.log(this.ob);
      this.$emit('onchecked', this.ob)
    }
  }
}



