import Worktype from '@/components/labour/Worktype.vue'

export default {
  name: 'Subcontracor',
  components: {
    Worktype
  },
  props: {
    obj: {
      type: Object,
            // 对象或数组默认值必须从一个工厂函数获取
      default: function () {
        return {
          id: '1580010143400535',
          name: '广联达建设一局',
          list: []
        }
      }
    }
  },
  data () {
    return {
      disChildren: true
    }
  },
  mounted () {
  },
  methods: {
    checked (ob) {
      this.$emit('onchecked', ob)
    },
    groupclicked () {
      console.log('groupclicked')
      this.disChildren = !this.disChildren
    }
  }
}



